<template>
  <div ref="root">
    <title-zone ref="titleZone"></title-zone>
    <content-zone ref="contentZone"></content-zone>
    <footer-zone :authorInfo="footerText" ref="footerZone"></footer-zone>
  </div>
</template>

<script>

import TitleZone from './components/body/TitleZone.vue';
import FooterZone from './components/body/FooterZone.vue';
import ContentZone from './components/body/ContentZone.vue';

export default {
  name: 'app',
  components: {
    TitleZone,
    ContentZone,
    FooterZone,
  },
  data(){
      return {
          footerText: "Author is GSan QQ: 490854014",
      }
  },

  mounted(){
    let windowLeftHeight = window.screen.height;
    
    //窗口剩余高度
    windowLeftHeight -= parseFloat(this.$refs.titleZone.$refs.root.offsetHeight);

    window.console.log("offsetHeight is " + this.$refs.titleZone.$refs.root.offsetHeight);
    window.console.log("titleZoneMinHeight is " + this.$refs.titleZone.titleZoneMinHeight);

    //做整体界面适配行为
    windowLeftHeight -= parseFloat(this.$refs.footerZone.$refs.root.offsetHeight);
    window.console.log("windowLeftHeight2 is "+ windowLeftHeight);

    //最后设置内容板块的高度
    this.$refs.contentZone.$refs.root.style["min-height"] = windowLeftHeight + "px";
  }
}

</script>

<style lang="scss">

[v-cloak] {
    display: none;
}

html, body {
    margin: 0;
    padding: 0;
}

</style>
